import React, { useEffect, useState } from 'react';
import StateBar from '../StateBar';
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import moment from 'moment-timezone';
import styled from 'styled-components';

const DivContainer = styled.div`
  width: 195px;
  height: 50%;
`;

const DivLeft = styled.div`
  float: left;
  width: 45px;
`;

const DivRight = styled.div`
  width: 150px;
  float: right;
`;

const colorMapping = {
    'ACTIVE': '#53D73F',
    'READY': '#428bca',
    'UNAVAILABLE': 'gray',
    'INTERRUPTED': '#428bca',
    'PROGRAM_STOPPED': '#428bca',
    'OPTIONAL_STOP': '#428bca',
    'FEED_HOLD': '#EB506B'
};

moment.tz.setDefault('UTC');

const ExecutionBar = ({ machineRef, start, end, height }) => {

    //todo: need to sort by start date
    const query = gql`
    query GetStates($machineRef: Int!, $start: timestamp!, $end: timestamp!) {
        executionIntervals(args: {machineRef: $machineRef, windowStart: $start, windowEnd: $end}, order_by: {start: asc}) {
            value
            start
            end
        }
    }
    `;

    const { loading, error, data } = useQuery(query, 
        {
            variables: {
                machineRef,
                start: start,
                end: end
            },
            skip: !machineRef
        }
    );

    const [points, setPoints] = useState([]);
    const [utilization, setUtilization] = useState();
    useEffect(() => {
        let totalInCycle = 0;
        const events = data ? data.executionIntervals : [];
        const pts = events.map((event,i) => {
            let newStart = (i === 0 && moment.utc(event.start).isBefore(moment.utc(start))) ? moment.utc(start) : moment.utc(event.start);
            let newEnd = (event.end && i < events.length - 1) ? moment.utc(event.end) : moment.utc(end);
            if (event.value === "ACTIVE") {
                const cycleTime = (newEnd ? newEnd.diff(newStart) : moment.utc().diff(newStart));
                totalInCycle += cycleTime;
            }
            return {
                start: moment.utc(newStart).valueOf(),
                end: moment.utc(newEnd).valueOf(),
                color: colorMapping[event.value]
            }
        }).filter(d => {
            return !(d.start===d.end);
        });
        setPoints(pts);
        setUtilization(totalInCycle/(end ? moment.utc(end).diff(moment.utc(start)) : moment.utc().diff(moment.utc(start))));
        
    }, [data, machineRef, start, end]);

    return (
        <div>
            { loading && 'Loading...' }
            { data && data.executionIntervals && (
            <DivContainer>
                <DivLeft>{utilization && Math.round(utilization * 1000)/10}%</DivLeft>
                <DivRight><StateBar data={points} min={(points && points[0]) ? points[0].start : points} max={(points && points[0]) ? points[points.length - 1].end : points} height={30}></StateBar></DivRight>
            </DivContainer> )}
            { error && (
                <DivContainer>
                {error.message}
                </DivContainer>
            )}
        </div>
        );
}

export default ExecutionBar;