import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import dataToPoints from './dataToPoints';

class StateBar extends PureComponent {

    static propTypes = {
        data: PropTypes.array,
        width: PropTypes.number,
        height: PropTypes.number,
        svgWidth: PropTypes.number,
        svgHeight: PropTypes.number,
        preserveAspectRatio: PropTypes.string,
        margin: PropTypes.number,
        style: PropTypes.object,
        min: PropTypes.object,
        max: PropTypes.object,
        onMouseMove: PropTypes.func
    };

    static defaultProps = {
        data: [],
        width: 240,
        height: 60,
        style: { fill: 'slategray' },
        //Scale the graphic content of the given element non-uniformly if necessary such that the element's bounding box exactly matches the viewport rectangle.
        preserveAspectRatio: 'none', //https://www.w3.org/TR/SVG/coords.html#PreserveAspectRatioAttribute
        margin: 2,
        onMouseMove: () => {},
    };

    render() {
        const {  data, width, height, svgWidth, svgHeight, preserveAspectRatio, margin, style, max, min, onMouseMove} = this.props;

        if (data.length === 0) return null;

        const points = dataToPoints({ data, width, height, margin, max, min });

        const svgOpts = { style: style, viewBox: `0 0 ${width} ${height}`, preserveAspectRatio: preserveAspectRatio };
        if (svgWidth > 0) svgOpts.width = svgWidth;
        if (svgHeight > 0) svgOpts.height = svgHeight;

        const fillStyle = {
            stroke: style.stroke || 'none',
            strokeWidth: '0',
            fillOpacity: style.fillOpacity || '.1',
            fill: style.fill || 'slategray',
            pointerEvents: 'auto',
          };

        const tooltips = points.map((p, i) => {
            return (
              <circle
                key={i}
                cx={p.x}
                cy={p.height/2}
                r={2}
                style={fillStyle}
                onMouseEnter={e => onMouseMove('enter', data[i], p)}
                onClick={e => onMouseMove('click', data[i], p)}
              />
            );
          });

        if (points && points.length > 0){
            return (
                <svg {...svgOpts}>
                    {
                        <g transform="scale(1,-1)">
                            {tooltips}
                            {points.map((p, i) =>
                            <rect
                                key={i}
                                x={p.x}
                                y={-height}
                                width={p.width}
                                height={height}
                                fill={p.color}
                                onMouseMove={onMouseMove && onMouseMove.bind(this, p)}
                            />,
                            )}
                        </g>
                    }
                </svg>
            );
        } else {
            return null;
        }
    }

};

export default StateBar;