import React from 'react';
import { useSubscription } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import styled from 'styled-components';
import Message from './Message';

const Pill = styled.div`
  padding: 2px;
  border-radius: 2px;
  color: black;
`;

const ActivePill = styled(Pill)`
  background-color: #53D73F;
  color: white;
`;

const IdlePill = styled(Pill)`
  border-style: solid;
  border-color: gray;
`;

const ExecutionStatus = ({ machineRef }) => {
  const query = gql`
    subscription GetCurrentExecution($machineRef: Int!) {
      currentExecution(args: {machineRef: $machineRef}) {
        value
        start
      }
    }
  `;

  const { data, loading, error } = useSubscription(query, {
    variables: {
      machineRef,
    },
    skip: !machineRef,
  });

  if (data && data.currentExecution && !!data.currentExecution.length && data.currentExecution[0].value === 'ACTIVE') {
    return (
      <div>
        { loading && 'Loading...' }
        { data && data.currentExecution && !!data.currentExecution.length && <ActivePill>{data.currentExecution[0].value}</ActivePill> }
        { error && (
            <Message>
              An error occured:
              <br />"{error.message}"
            </Message>
          )}
      </div>
    );
  } else {
    return (
      <div>
        { loading && 'Loading...' }
        { data && data.currentExecution && !!data.currentExecution.length && <IdlePill>{data.currentExecution[0].value}</IdlePill> }
        { error && (
            <Message>
              An error occured:
              <br />"{error.message}"
            </Message>
          )}
      </div>
    );
  }
};

export default ExecutionStatus;
