import React, { useEffect, useState } from 'react';
import _ from 'lodash';
import styled from 'styled-components';
import moment from 'moment-timezone';
import { useProductionQuery } from '../utils/hooks';
import { Sparklines, SparklinesBars, SparklinesLine } from 'react-sparklines';

const DivContainer = styled.div`
  width: 155px;
`;

const DivLeft = styled.div`
  float: right;
  width: 100px;
`;

const DivRight = styled.div`
  width: 50px;
  float: left;
`;

const MINUTE = 60 * 1000;

function getFill(data, start, end, period) {
  const length = moment.utc(end).diff(start, period);

  const items = (data && data.items) || [];

  const first = _.first(items);
  const last = _.last(items);

  const leadingZeroCount = first
    ? moment.utc(first[period]).diff(start, period)
    : length;

  const trailingZeroCount = last
    ? moment.utc(end).diff(last[period], period)
    : 0;

  const leadingZeros = _.times(leadingZeroCount, () => null);
  const trailingZeros = _.times(trailingZeroCount, () => null);

  return { leadingZeros, trailingZeros };
}

const Utilization = ({ period, length, machineId, type }) => {
  const start = moment.utc().startOf(period).subtract(length, period + 's').format();
  const end = moment.utc().endOf(period).format();

  const query = `{
    "start": "${start}",
    "end": "${end}",
    "data": [{
      "metric": "utilizationRate"
    }],
    "groupBy": [
      {"group": "${period}"}
    ],
    "filter": {
      "machine": ["${machineId}"]
    },
    "flatten": true
  }`;

  const { loading, error, data } = useProductionQuery(query, 5 * MINUTE);
  const [ sparkData, setSparkData ] = useState([]);

  useEffect(() => {
    const rateData = data && data.items
     ? data.items.map(item => item.utilizationRate)
     : [];

    const { leadingZeros, trailingZeros } = getFill(data, start, end, period);
    setSparkData([...leadingZeros, ...rateData, ...trailingZeros]);
  }, [data, length, period, start, end]);

  const SparkSettings = type === 'bar' ? SparklinesBars : SparklinesLine;

  return (
    <div>
      { loading && 'Loading...' }
      { data !== undefined && !!data.aggregate &&
      <DivContainer>
        <DivLeft>
          <Sparklines data={sparkData} max={1} min={0}>
            <SparkSettings style={{ fill: "#53D73F" }}/>
          </Sparklines>
        </DivLeft>
        <DivRight>
          {Math.round(data.aggregate.utilizationRate * 1000) / 10}%
        </DivRight>
      </DivContainer> }
      { error && (
          <DivContainer>
          <div>{error.message}</div>
          </DivContainer>
        )}
    </div>
  );
};

export default Utilization;
