const arrayMax  = ({ Math, data }) => 
    Math.max.apply(Math, data.map = (d => d.x));

const arrayMin  = ({ Math, data }) => 
    Math.min.apply(Math, data.map = (d => d.x));

export default ({ data, width = 1, height = 1, margin = 0, max = arrayMax(data), min = arrayMin(data) }) => {

    const len = max - min;

    const vheight = (height - margin * 2);
    const hfactor = (width - margin * 2) / len;

    const mappedPoints = data.map(d => ({
        x: (d.start - min) * hfactor,
        width: (d.end - d.start) * hfactor,
        height: vheight,
        color: d.color
    })).filter(d => {
        return d.width > 0;
    });

    return mappedPoints;
};
