import React, { useState, useEffect } from 'react';
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import styled from 'styled-components';
import moment from 'moment-timezone';
import StateBar from './StateBar';

const DivContainer = styled.div`
  width: 155px;
`;

const colorMapping = {
  ACTIVE: '#53D73F',
  READY: '#428bca',
  UNAVAILABLE: 'gray',
  INTERRUPTED: '#428bca',
  STOPPED: '#428bca',
  PROGRAM_STOPPED: '#428bca',
};

moment.tz.setDefault('UTC');

const computeTimeWindow = (hours) => {
  const end = moment().endOf('minute');
  const start = moment(end).subtract(hours, 'hours');
  return { start, end };
};

const ExecutionTimeline = ({ machineRef, hours, height }) => {
  const query = gql`
    query getExecutionIntervals(
      $machineRef: Int!
      $start: timestamp!
      $end: timestamp!
    ) {
      executionIntervals(
        args: { machineRef: $machineRef, windowStart: $start, windowEnd: $end }
      ) {
        start
        end
        value
      }
    }
  `;

  const [timeWindow, setTimeWindow] = useState(computeTimeWindow(hours));

  const options = {
    variables: {
      machineRef,
      ...timeWindow,
    },
  };
  const { loading, error, data } = useQuery(query, options);
  const executionIntervals = data ? data.executionIntervals : [];

  useEffect(() => {
    const interval = setInterval(() => {
      if (!loading) {
        setTimeWindow(computeTimeWindow(hours));
      }
    }, 30000);
    return () => clearInterval(interval);
  }, [loading, hours, machineRef]);

  const points = executionIntervals.map((executionInterval) => ({
    start: moment.utc(executionInterval.start).isBefore(timeWindow.start)
      ? timeWindow.start
      : moment.utc(executionInterval.start),
    end: executionInterval.end
      ? moment.utc(executionInterval.end)
      : timeWindow.end,
    color: colorMapping[executionInterval.value],
  }));

  return (
    <div>
      {!data && 'Loading...'}
      {data && data.executionIntervals && (
        <DivContainer>
          <StateBar
            data={points}
            height={height}
            min={timeWindow.start}
            max={timeWindow.end}
          ></StateBar>
        </DivContainer>
      )}
      {error && <DivContainer>{error.message}</DivContainer>}
    </div>
  );
};

export default ExecutionTimeline;
