import React, { useState } from 'react';
import { request } from '../../utils/request';

const CreateJobButton = ({ machineId, start, end, part, getMedian, hasEnd }) => {
    const jobRunId = `runId:${start}:${end}`;
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState();
    const startUrl = `${process.env.REACT_APP_API_URL}/erp/jobdispatch/start`;
    const endUrl = `${process.env.REACT_APP_API_URL}/erp/jobdispatch/stop`;

    async function createJob() {
        const startProps = `{
            "erpJobOperationRunId": "${jobRunId}",
            "erpMachineId": "${machineId}",
            "jobName": "${part}",
            "startAt": "${start}",
            "expectedPartTime": ${getMedian()},
            "idealPartTime": ${getMedian()}
        }`;

        const endProps = `{
            "erpJobOperationRunId": "${jobRunId}",
            "endAt": "${end}"
        }`;

        console.log('posting', startProps, endProps);
        setLoading(true);
        try {
        await request(startUrl, {
            method: 'POST',
            body: startProps,
        });

        hasEnd && await request(endUrl, {
            method: 'POST',
            body: endProps,
        });

        } catch(error) {
            setError(error);
            console.log('error',error);
        } finally {
            setLoading(false);
        }
    }

    return (
        <div>
            <button onClick={createJob}>{loading ? 'Creating job...' : 'Create Job'}</button>
            {error && 'Error!'}
        </div>
    )
};

export default CreateJobButton;
