import React from 'react';
import { useMachinesQuery } from '../utils/hooks';
import List from './List';
import Message from './Message';

const Home = () => {
  const { loading, error, data } = useMachinesQuery();

  return (
    <div>
      <div>
        {loading && <Message>Loading...</Message>}
        {data && <List items={data} />}
        {error && (
          <Message>
            An error occured:
            <br />"{error.message}"
          </Message>
        )}
      </div>
    </div>
  );
};

export default Home;
