import React, { useEffect, useState } from 'react';
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import moment from 'moment-timezone';
import styled from 'styled-components';
import PartCount from './PartCount';
import ExecutionBar from './ExecutionBar';
import CreateJobButton from './CreateJobButton';
import "react-datepicker/dist/react-datepicker.css";

const TableStyle = styled.div`
table {
  border: 1px solid #FFFFFF;
  width: 100%;
  text-align: left;
  vertical-align: bottom;
  border-collapse: collapse;
}
table td, table th {
  border: 1px solid #FFFFFF;
  padding: 3px 2px;
}
table tbody td {
  font-size: 13px;
}
table tr:nth-child(even) {
  background: #E0F5DD;
}
table thead {
  background: #09700B;
  border-bottom: 5px solid #FFFFFF;
}
table thead th {
  font-size: 17px;
  font-weight: bold;
  color: #FFFFFF;
  text-align: left;
  border-left: 2px solid #FFFFFF;
}
table thead th:first-child {
  border-left: none;
}

table tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #333333;
  background: #D2F5E1;
  border-top: 3px solid #444444;
}
table tfoot td {
  font-size: 14px;
}`

moment.tz.setDefault('EDT');

const Machine = ({ machineRef, machineId, stateItem, startDate, endDate, mergeRuns, showHeader, partRegEx, partCountKey, commentNumber }) => {
  const queryGetCompanyInfo = gql`
  query GetCompanyInfo {
    Company {
      timezone
    }
  }
  `;
  const { data: dataCompanyInfo } = useQuery(queryGetCompanyInfo);
  const [timezone, setTimeZone] = useState('America/New_York');
  useEffect(() =>{
    if (dataCompanyInfo) {
      setTimeZone(dataCompanyInfo.Company[0].timezone);
    }
  }, [dataCompanyInfo, timezone]);

    const query = gql`
    query Program_Comment($machineRef: Int!, $key: String!, $start: timestamp!, $end: timestamp!) {
        stateIntervals(args: {machineRef: $machineRef, metricKey: $key, windowStart: $start, windowEnd: $end},
        order_by: {start: asc},
        where: {_not: {value: {_eq: "UNAVAILABLE"}}}) {
          value
          start
          end
        }
      }
    `;
    const { loading, error, data } = useQuery(query, {
        variables: {
            machineRef,
            key: stateItem.key,
            start: startDate,
            end: endDate,
        },
    });
    const [points, setPoints] = useState([]);
    // Logic here is if there is a program that's less than a minute (or unavailable for any timeframe),
    // it's filtered out. After the filter if the program is adjoining, we merge them.
    useEffect(() => {
        const rxProgram = /\ \s*[^ (]*/;
        if(data && data.stateIntervals && data.stateIntervals[0]){
            console.log('data', data);
            const newData = data.stateIntervals.filter((item) => (
                (item.end ? moment(item.end).diff(moment(item.start),'minutes') > 1 : true)
            )).map((item) => {
              let s = stateItem.programType === "header" ? item.value.match(partRegEx) == null ? item.value.match(rxProgram) == null ? item.value : item.value.match(rxProgram)[0] : item.value.match(partRegEx)[commentNumber-1] : item.value;
              return {
                  start: item.start,
                  end: item.end ? item.end : moment(),
                  hasEnd: !!item.end,
                  value: item.value,
                  part: s,
                  program: stateItem.programType === "header" ? item.value.match(rxProgram) == null ? null : item.value.match(rxProgram) == null ? item.value : item.value.match(rxProgram)[0] : item.value
              }
            });
            const mergedData = [];
            if (mergeRuns){
                for (let i = 0; i < newData.length; i++){
                    if (i > 0 && (mergedData[mergedData.length-1].part ? mergedData[mergedData.length-1].part === newData[i].part : mergedData[mergedData.length-1].program === newData[i].program)) {
                        mergedData[mergedData.length-1].end = newData[i].end;
                        i++;
                    } else {
                        mergedData.push(newData[i]);
                    }
                }
                //console.log('mergedData', mergedData);
                setPoints(mergedData);
            } else {
                setPoints(newData);
            }
        }
    }, [data, mergeRuns, stateItem, partRegEx, endDate, commentNumber]); //don't put mergeruns in here or we'll get an endless loop

    const setMedian = (item) => {
      return (median) => {
        item.median = median;
      };
    };

    const getMedian = (item) => {
      return () => {
        return item.median;
      };
    };

    return (
      <div>
        { loading && <div>Loading...</div> }
        { points && points.length <= 0 && !loading && <div>No Program Headers</div> }
        { !loading && points && points.length > 0 &&
            <TableStyle>
              <div>
                  <table>
                      <thead>
                      <tr>
                          {showHeader || stateItem.programType !== "header" ? <th>Program</th> : <></>}
                          {stateItem.programType === "header" ? <th>Part</th>:<></>}
                          <th>Run Time</th>
                          <th>Duration</th>
                          <th>Utilization</th>
                          <th>Parts</th>
                          <th>Median Cycle</th>
                          <th></th>
                      </tr>
                      </thead>
                      <tbody>
                      {points && points.length > 0 && points.map((item) => (
                      <tr key={item.start}>
                        {showHeader || stateItem.programType !== "header" ? <td>{item.value}</td> : <></>}
                        {stateItem.programType === "header" ? <td>{item.part}</td>:<></>}
                        <td>{moment.utc(item.start).tz(timezone).format('lll')} - {item.hasEnd ? moment.utc(item.end).tz(timezone).format('lll') : 'in progress'}</td>
                        <td>{moment.duration(moment(item.end).diff(moment(item.start))).humanize()}</td>
                        <td><ExecutionBar machineRef={machineRef} start={item.start} end={item.end} height={30}/></td>
                        <PartCount machineRef={machineRef} partMetric={partCountKey} start={item.start} end={item.end} setMedian={setMedian(item)} />
                        <td><CreateJobButton machineRef={machineRef} machineId={machineId} start={item.start} end={item.end} part={item.part} getMedian={getMedian(item)} hasEnd={item.hasEnd} /></td>
                      </tr>
                      ))}
                      </tbody>
                  </table>
              </div>
          </TableStyle>
        }
      </div>
    );
};

export default Machine;
