import React, { useEffect, useState } from 'react';
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import moment from 'moment-timezone';

function median(values){
    if(values.length ===0) return 0;
    if(values.length ===1) return values[0];

    values.sort(function(a,b){
      return a-b;
    });

    var half = Math.ceil(values.length / 2); //ceil because if 2 cycles, the first is often munged due to setup

    if (values.length % 2)
      return values[half];

    return (values[half - 1] + values[half]) / 2.0;
  }

const PartCount = ({ machineRef, partMetric, start, end, setMedian }) => {
    const query = gql`
    query PartCount($machineRef: Int!, $partMetric: String!, $start: timestamp!, $end: timestamp!) {
        counts(args: {machineRef: $machineRef, metricKey: $partMetric, windowStart: $start, windowEnd: $end}, order_by: {eventTime: asc}) {
              value
              eventTime
            }
          }
    `;

    const { loading, error, data } = useQuery(query, {
        variables: {
            machineRef,
            partMetric,
            start,
            end
        },
        skip: !partMetric,
    });

    const [medianCycle, setMedianCycle] = useState(0);
    useEffect(() => {
        if (data && data.counts && data.counts.length > 0){
            const newArr = [];
            for (let i = 0; i < data.counts.length; i++){
                //the first cycle time is calculated off of the start of the program
                newArr.push(moment.utc(data.counts[i].eventTime).diff(moment.utc((i===0) ? start : data.counts[i-1].eventTime)));
            };
            const med = median(newArr);
            setMedianCycle(med);
            setMedian(med);
        } else {
            setMedian(0);
            setMedianCycle(0);
        }
    }, [data, machineRef, setMedian]);

    return (
        <>
            { loading && ( <><td>Loading...</td><td></td></> )}
            { data && data.counts && (
            <>
                <td>{data.counts.length}</td><td>{(moment.duration(medianCycle).minutes() > 0) ? moment.duration(medianCycle).minutes() + 'm' : ''} {moment.duration(medianCycle).seconds()}s</td>
            </> )}
            { error && (
                <>
                <td>{error.message}</td><td></td>
                </>
            )}
        </>
        );
};

export default PartCount;
